export const setProducts = ({ dispatch, state }, self) => {
    Mock.mock(/products.json/, {
        'state': 200,
        'notice': 'VuejsSPA',
        'genres|3-6': [{
            'cid|+1': 10,
            'title|1': ['','','','','','',''],
            'quantity': 0,
        }],
        'list|1-10': [{
            'cid|+1': 10,
            'fid|+1': 1000,
            'title|1': ['','','','','','()','',
            '()','<>','-','-',''],
            'inventory|0-100': 100,
            'quantity': 0,
            'price|5-30': 30,
            'sprice|10-50': 50,
            'sold|1-100':100,
            'hasattr|1-2': true,
            'type|1': ['1','2','3'], //
            //'hasattr': true,
            'img': Random.image('100x80', '#50B347', '#FFF', 'Mock.js')
        }],
    })
    __POST('products.json', {},
        (response) => {
            dispatch('RECEIVE_PRODUCTS', response.list)
            dispatch('RECEIVE_GENRES', response.genres)
            self.notice = response.notice
            self.computeSize()
            dispatch('SET_TOAST', { show: false })
        },
        (err) => {
            console.log(err)
        }
    );
}


