export const setLocation = ({ dispatch, state }, self) => {
    Mock.mock(/orderAddressLocation1.json/, {
        "state": 200,
        "list": [
            {id: "12414", title: "", mode: "2"},
            {id: "12267", title: "", mode: "1"},
            {id: "11456", title: "", mode: "1"},
            {id: "12415", title: "", mode: "2"},
        ],
    });
    Mock.mock(/orderAddressLocation2.json/, {
        "state": 200,
        "list|1-2": [
            {id: "10597", title: "", mode: "1"},
            {id: "10598", title: "", mode: "1"}
        ],
    });
    Mock.mock(/orderAddressLocation3.json/, {
        "state": 200,
        "list|1-4": [
            {id: "10598", title: "101", mode: "1"},
            {id: "10598", title: "102", mode: "1"},
        ],
    });

    let url = ''
    if(self.lid === "0") {
        url = 'orderAddressLocation1.json'
    }else if(self.lid === '12267'){
        url = 'orderAddressLocation2.json'
    }else if(self.lid === '12415'){
        url = 'orderAddressLocation3.json'
    }

    __POST(url, {id: self.lid},
        (response) => {
            console.log("list", response.list)
            self.location.push(response.list)
            self.ifUpdate = false
        },
        (err) => {
            console.log(err)
        }
    );
}

